import React from 'react';
import {HeaderWidget, FooterWidget} from "./widget/base";
import {HashRouter as Router, Switch, Route, Redirect,} from "react-router-dom";
import {CoversPage} from "./view/covers";
import styled, {ThemeProvider} from "styled-components";
import {WidthStyle} from "./widget/style/width_style";
import {themes, theme} from "./theme";
import {ThemeProvider as MuiThemeProvider} from '@material-ui/core/styles';
import {ModelProvider} from "./provider/model_provider";
import {StartMiningPage} from "./view/start_mining";
import {StartListPage} from "./view/stake_list";
import {useWallet, UseWalletProvider} from './use_wallet'
import {WalletConnect} from "./widget/modal/wallet_connect";
import {Web3Provider} from "./provider/web3_provider";
import {BackdropProvider} from "./provider/backdrop_provider";
import {AppConfig} from "./config";
import PubBgImg from "./assets/imgs/pub_bg.png"
import {InvitationWidget} from "./view/covers/Invitation_list";
import {Provider} from "react-redux";
import store from "./state";

const App: React.FC = () => {
    return (
        <ProviderWidget>
            <div className="App">
                <Page/>
            </div>
        </ProviderWidget>
    );
}


const ProviderWidget: React.FC = ({children}) => {
    return (
        <UseWalletProvider chainId={AppConfig.chainId} connectors={{
            walletconnect: {rpcUrl: AppConfig.rpcUrl[AppConfig.chainId]},
            ledger: {url: AppConfig.rpcUrl[AppConfig.chainId]},

        }}>
            <ThemeProvider theme={theme}>
                <MuiThemeProvider theme={themes}>
                    <Web3Provider>
                        <Provider store={store}>
                            <BackdropProvider>
                                <ModelProvider>
                                    {/*<div style={{height:"30px"}} />*/}
                                    {/*<WarningStyle>WARNING: Internal testing ONLY, do NOT interact with this*/}
                                    {/*    page!</WarningStyle>*/}
                                    {children}
                                </ModelProvider>
                            </BackdropProvider>
                        </Provider>


                    </Web3Provider>

                </MuiThemeProvider>

            </ThemeProvider>

        </UseWalletProvider>

    );
}


const Page: React.FC = () => {

    const {status} = useWallet();


    return (<PubBg>
        {
            status === 'connected' ? <Router>
                <HeaderWidget/>

                <MainStyle>

                    <Switch>
                        <Route path="/cover/">
                            <CoversPage/>
                        </Route>
                        <Route path="/start_mining/">
                            <StartMiningPage/>
                        </Route>
                        <Route path="/start_list/">
                            <StartListPage/>
                        </Route>
                        <Route path="/referral/">
                            <InvitationWidget/>
                        </Route>
                        <Route path="/stats">
                            {/*<UnNamePage/>*/}
                        </Route>
                        {/*<Route path="/">
                              </Route>*/}
                        <Redirect to="/start_mining/"/>

                    </Switch>
                </MainStyle>
                <FooterWidget/>
            </Router> : <div
                style={{
                    alignItems: 'center',
                    display: 'flex',
                    flex: 1,
                    justifyContent: 'center',
                    height: '100vh'
                }}
            >
                <WalletConnect/>


            </div>

        }
    </PubBg>);
};

const WarningStyle = styled.div`
  background-color: #FED6DE;
  color: #EC1937;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;

`

const MainStyle = styled.div`
  ${WidthStyle.MaxWidth};
  min-height: 70vh;
  margin: 40px auto 40px auto;

  @media (max-width: ${({theme}) => theme.lg}) {
    margin: 20px auto 20px auto;

    //margin: 20px auto 20px auto;
  }

  .airDrop {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 10px;
  }
`;

const PubBg = styled.div`
    //background-image: url(${PubBgImg});
  background-size: 100% 100%;
  background-repeat: no-repeat;
`
export default App;
